import { Component, OnInit } from '@angular/core';
import { BaseService } from 'src/app/services/base.service';
import { AccessTokenModel, DeptModel, ProcessModel } from 'src/app/data-model/base.model';
import { Constants, WSTypeCode } from 'src/app/data-model/constant.model';
import { Storage } from '@ionic/storage';
import { Router } from '@angular/router';
import { NavController, LoadingController, AlertController } from '@ionic/angular';
import { BasePagePage } from 'src/app/shared/base-page/base-page.page';

@Component({
  selector: 'app-wip-dept-selection',
  templateUrl: './wip-dept-selection.page.html',
  styleUrls: ['./wip-dept-selection.page.scss'],
})
export class WipDeptSelectionPage extends BasePagePage implements OnInit {

  arrDept: Array<any> = [];
  deptReqModel: DeptModel = new AccessTokenModel();
  checkBoxList: Array<any> = [];
  isIndeterminate: boolean;
  masterCheck: boolean;
  processReqModel: ProcessModel = new ProcessModel(); // for next page use

  constructor(
    public loadingCtrl: LoadingController, 
    public alertCtrl: AlertController,
    public navCtrl: NavController,
    private service: BaseService,
    private storage: Storage,
    private router: Router
  ) {

    super(loadingCtrl, alertCtrl);

    this.storage.get(Constants.c_ACCESS_TOKEN).then((accessToken) => {

      this.deptReqModel.accesstoken = accessToken;
      this.processReqModel.accesstoken = accessToken;

      this.service.presentLoading();

      this.service.callWebService(Constants.k_GET_DEPT, this.deptReqModel, (res) => {

        this.arrDept = JSON.parse(res.data);

        this.arrDept.forEach(element => {

          this.checkBoxList.push({ 'dept': element, 'isChecked': false })

        });

        this.service.dismissLoading();

      });

    });

  }

  ngOnInit() {
  }

  checkMaster() {
    setTimeout(() => {
      this.checkBoxList.forEach(obj => {
        obj.isChecked = this.masterCheck;
      });
    });
  }

  checkEvent() {
    const totalItems = this.checkBoxList.length;
    let checked = 0;
    this.checkBoxList.map(obj => {
      if (obj.isChecked) checked++;
    });
    if (checked > 0 && checked < totalItems) {
      //If even one item is checked but not all
      this.isIndeterminate = true;
      this.masterCheck = false;
    } else if (checked == totalItems) {
      //If all are checked
      this.masterCheck = true;
      this.isIndeterminate = false;
    } else {
      //If none is checked
      this.isIndeterminate = false;
      this.masterCheck = false;
    }
  }

  async onNextClicked() {
    // console.log("CHOSEN: ", this.checkBoxList);
    // this.router.navigateByUrl('/wip/process-selection')
    // this.navCtrl.navigateForward('/wip/process-selection');

    var isCheckedAtLeastOne: boolean = false;
    this.processReqModel.dept = [];

    this.checkBoxList.forEach(element => {
      if (element.isChecked) {
        isCheckedAtLeastOne = true
        this.processReqModel.dept.push(element.dept.value)
      }
    });

    if (isCheckedAtLeastOne) {
      this.service.setData('sel-dept', this.processReqModel);
      this.navCtrl.navigateForward('/wip/process-selection/sel-dept');
    } else {
      BasePagePage.presentAlert('Alert', 'Please select at least one (1) department.');
    }

  }

}
